import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        // 声明周期钩子
        this.model = { details: '', type: 'type1' };
        this.businessId = '';
        // tab切换配置
        this.selectedLabelDefault = 'unprocessed';
        this.tabBarList = [{
                label: '未处理',
                value: 'unprocessed'
            }, {
                label: '已处理',
                value: 'processed'
            }];
        // 
        this.page = 1;
        this.isShowMore = true;
        this.repairList = [];
    }
    activated() {
        // this.getWeiXinConfig()
        // this.$store.commit('SET_SESSION_ID', 'noSession')
        this.pageInit();
    }
    pageInit() {
        let query = this.$route.query;
        if (query.accountId) {
            this.businessId = query.accountId;
            this.getCommodityOrderList();
        }
        else if (query.code) {
            this.getUserInfo();
        }
    }
    validateHandler(result) {
        this.validity = result.validity;
        this.valid = result.valid;
        console.log(result);
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    getUserInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '数据获取中...'
            });
            toast.show();
            this.$store.dispatch('getCommodityUserInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.getCommodityOrderList();
                        if (response.data.userInfo) {
                            this.userInfo = response.data.userInfo;
                        }
                        // this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } })
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('不是微信登陆');
        }
    }
    tabBarChange(value) {
        this.page = 1;
        this.repairList = [];
        this.isShowMore = true;
        this.getCommodityOrderList();
    }
    get houseOptions() {
        this.$store.state.houseList.forEach((obj) => {
            obj.value = obj.id;
            obj.text = obj.account_info[0].name + ' ' + obj.build_info[0].name + ' ' + obj.unitNum + '单元' + obj.floorNum + '层' + obj.roomNum + '室';
        });
        return this.$store.state.houseList;
    }
    get userInfo() {
        return this.$store.state.userInfo;
    }
    getCommodityOrderList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getCommodityOrderList', { businessId: this.businessId, page: this.page, ordertype: this.selectedLabelDefault, type: 'staff' }).then((response) => {
            if (response.success) {
                console.log(response.data.list);
                if (response.data.list.length > 0) {
                    this.repairList = this.repairList.concat(response.data.list);
                }
                else {
                    this.isShowMore = false;
                }
            }
            toast.hide();
        });
    }
    showMoreClick() {
        this.page++;
        this.getCommodityOrderList();
    }
    updateRepairState(id) {
        this.$store.dispatch('updateCommodityOrderState', { out_trade_no: id }).then((response) => {
            if (response.success) {
                this['$createDialog']({
                    type: 'alert',
                    content: '处理完成',
                    icon: 'cubeic-ok'
                }).show();
                this.tabBarChange();
            }
        });
    }
};
__decorate([
    Prop({ default: 'default value' })
], Home.prototype, "propB", void 0);
Home = __decorate([
    Component({
        components: {},
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            },
            getPhone(item) {
                if (item && item.user_info) {
                    return item.user_info[0].phone;
                }
                else {
                    return '';
                }
            },
            getAddress(item) {
                if (item && item.user_info) {
                    return item.user_info[0].address;
                }
                else {
                    return '';
                }
            }
        }
    })
], Home);
export default Home;
